<template>
  <div>
    <van-nav-bar
      title="注册"
      cross
      @click-right="$router.push('/login')"
      right-text="登录"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="user.phone"
        name="手机号"
        label="手机号"
        placeholder="手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <van-field
        v-model="user.nickname"
        name="昵称"
        label="昵称"
        placeholder="昵称"
        :rules="[{ required: true, message: '请填写昵称' }]"
      />
      <van-field
        v-model="user.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请再次输入密码' }]"
      />
      <van-field
        v-model="agin"
        type="password"
        name="a"
        label="确认密码"
        placeholder="确认密码"
        :rules="[{ required: true, message: '请再次填写密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import {register} from "../util/request"
export default {
  data() {
    return {
      user: {
        phone: "",
        password: "",
        nickname: "",
      },
      agin: "",
    };
  },
  methods: {
    onSubmit() {
      if (this.user.password !== this.agin) {
          return
      }
      register(this.user).then(data=>{
         if(data.code==200){
            this.$toast.success('注册成功');
          this.$router.push("/login")
         }else{
             this.$toast.fail('注册失败');
         }
      })
    },
  },
};
</script>

<style scoped>
.van-form {
  padding: 0 10px;
}
</style>


